.startPage {
    margin: auto 170px;
    border-bottom: 1px solid darkslategrey;
}

.item {
    color:black;
    text-decoration: none;
    padding-left: 100px;
}

.active {
    color: aquamarine;
    text-decoration: none;
}

.grid {
    margin: 0 auto;
    width: 1200px;
    display: grid;
    grid-template-areas: "n c";
    grid-template-columns: 6fr 6fr;
}

.buttonLeft01 {
   /* padding-left: 560px;*/
    margin-top: 12px;
    grid-area: n;
    text-align: center;
}

.buttonLeft02 {
    margin-top: 12px;
    /*margin-left: 870px;*/
    grid-area: c;
    text-align: center;
}

.buttonLeft04 {
    margin-top: -70px;
    text-align: center;
    padding-bottom: 50px;
}

.buttonLeft03 {
    margin-top: 12px;
